<template>
  <VaCard>
    <VaCardTitle class="flex justify-between items-center">
      <div class="flex items-center gap-2">
        <VaIcon name="view_kanban" color="primary" />
        <span>Sales Pipeline</span>
      </div>
      <div class="flex items-center gap-3">
        <VaSelect
          v-model="seasonFilter"
          :options="seasonOptions"
          placeholder="All Seasons"
          class="w-48"
          clearable
          @update:modelValue="loadPipeline"
        />
        <VaButton preset="secondary" icon="refresh" :loading="loading" @click="loadPipeline"> Refresh </VaButton>
      </div>
    </VaCardTitle>

    <VaCardContent>
      <!-- Loading State -->
      <div v-if="loading && !hasData" class="loading-container py-8">
        <VaProgressBar indeterminate />
        <p class="text-center mt-4 text-gray-600">Loading pipeline...</p>
      </div>

      <!-- Pipeline Columns -->
      <div v-else class="pipeline-columns">
        <!-- New Inquiries Column -->
        <div class="pipeline-column">
          <div class="column-header" :style="{ borderColor: '#f59e0b' }">
            <div class="column-title">
              <VaIcon name="fiber_new" color="warning" />
              <span>New Inquiries</span>
            </div>
            <VaBadge :text="String(counts.new_inquiries)" color="warning" />
          </div>
          <div class="column-content">
            <div v-if="pipeline.new_inquiries.length === 0" class="empty-column">
              <VaIcon name="inbox" size="2rem" color="secondary" />
              <span>No new inquiries</span>
            </div>
            <PipelineCard
              v-for="item in pipeline.new_inquiries"
              :key="`new-${item.id}`"
              :item="item"
              @createProposal="handleCreateProposal(item)"
              @click="handleCardClick(item)"
            />
          </div>
        </div>

        <!-- Quotation Column (formerly Pending) -->
        <div class="pipeline-column">
          <div class="column-header" :style="{ borderColor: '#f59e0b' }">
            <div class="column-title">
              <VaIcon name="hourglass_empty" color="warning" />
              <span>Quotation</span>
            </div>
            <VaBadge :text="String(counts.pending)" color="warning" />
          </div>
          <div class="column-content">
            <div v-if="pipeline.pending.length === 0" class="empty-column">
              <VaIcon name="hourglass_empty" size="2rem" color="secondary" />
              <span>No quotation items</span>
            </div>
            <PipelineCard
              v-for="item in pipeline.pending"
              :key="`pending-${item.id}`"
              :item="item"
              @view="handleViewProposal(item)"
              @edit="handleEditProposal(item)"
              @click="handleCardClick(item)"
            />
          </div>
        </div>

        <!-- Provision Sales Column -->
        <div class="pipeline-column">
          <div class="column-header" :style="{ borderColor: '#3b82f6' }">
            <div class="column-title">
              <VaIcon name="pending_actions" color="info" />
              <span>Provision Sales</span>
            </div>
            <VaBadge :text="String(counts.provision_sales)" color="info" />
          </div>
          <div class="column-content">
            <div v-if="pipeline.provision_sales.length === 0" class="empty-column">
              <VaIcon name="pending_actions" size="2rem" color="secondary" />
              <span>No provisional sales</span>
            </div>
            <PipelineCard
              v-for="item in pipeline.provision_sales"
              :key="`provision-${item.id}`"
              :item="item"
              @view="handleViewProposal(item)"
              @edit="handleEditProposal(item)"
              @click="handleCardClick(item)"
            />
          </div>
        </div>

        <!-- Confirmed Column (Sales Confirmation - Only confirmed packages) -->
        <div class="pipeline-column confirmed-column">
          <div class="column-header" :style="{ borderColor: '#10b981' }">
            <div class="column-title">
              <VaIcon name="check_circle" color="success" />
              <span>Sales Confirmation</span>
            </div>
            <VaBadge :text="String(counts.confirmed)" color="success" />
          </div>
          <div class="column-content">
            <div v-if="pipeline.confirmed.length === 0" class="empty-column">
              <VaIcon name="check_circle" size="2rem" color="secondary" />
              <span>No confirmed packages</span>
            </div>
            <ConfirmedPackageCard
              v-for="item in pipeline.confirmed"
              :key="`confirmed-${item.id}`"
              :item="item"
              @view="handleViewProposal(item)"
              @click="handleCardClick(item)"
            />
          </div>
        </div>

        <!-- Cancelled Column -->
        <div class="pipeline-column">
          <div class="column-header" :style="{ borderColor: '#6b7280' }">
            <div class="column-title">
              <VaIcon name="cancel" color="secondary" />
              <span>Cancelled</span>
            </div>
            <VaBadge :text="String(counts.cancelled)" color="secondary" />
          </div>
          <div class="column-content">
            <div v-if="pipeline.cancelled.length === 0" class="empty-column">
              <VaIcon name="cancel" size="2rem" color="secondary" />
              <span>No cancelled items</span>
            </div>
            <PipelineCard
              v-for="item in pipeline.cancelled"
              :key="`cancelled-${item.id}`"
              :item="item"
              @view="handleViewProposal(item)"
              @click="handleCardClick(item)"
            />
          </div>
        </div>

        <!-- Completed Column -->
        <div class="pipeline-column">
          <div class="column-header" :style="{ borderColor: '#6366f1' }">
            <div class="column-title">
              <VaIcon name="task_alt" color="primary" />
              <span>Completed</span>
            </div>
            <VaBadge :text="String(counts.completed)" color="primary" />
          </div>
          <div class="column-content">
            <div v-if="pipeline.completed.length === 0" class="empty-column">
              <VaIcon name="task_alt" size="2rem" color="secondary" />
              <span>No completed items</span>
            </div>
            <PipelineCard
              v-for="item in pipeline.completed"
              :key="`completed-${item.id}`"
              :item="item"
              @view="handleViewProposal(item)"
              @click="handleCardClick(item)"
            />
          </div>
        </div>
      </div>

      <!-- Proposal View Modal -->
      <VaModal v-model="showViewModal" size="large" :hide-default-actions="true" title="Proposal Details">
        <ProposalView
          v-if="currentProposal"
          :proposal="currentProposal"
          @edit="handleEditFromView"
          @statusChange="handleStatusChange"
        />
        <div class="flex justify-end mt-4">
          <VaButton preset="secondary" @click="showViewModal = false">Close</VaButton>
        </div>
      </VaModal>

      <!-- Proposal Form Modal -->
      <VaModal
        v-model="showFormModal"
        size="large"
        :hide-default-actions="true"
        :title="isEditMode ? 'Edit Quotation' : 'Create Quotation'"
      >
        <ProposalForm
          :proposal="editingProposal"
          :is-edit="isEditMode"
          :preselected-inquiry="preselectedInquiry"
          @save="handleSaveProposal"
          @cancel="showFormModal = false"
        />
      </VaModal>
    </VaCardContent>
  </VaCard>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { mapActions, mapState } from 'pinia'
import { useProposalStore } from '../../../stores/proposal-store'
import { useToast } from 'vuestic-ui'
import axios from 'axios'
import PipelineCard from './components/PipelineCard.vue'
import ConfirmedPackageCard from './components/ConfirmedPackageCard.vue'
import ProposalView from './components/ProposalView.vue'
import ProposalForm from './components/ProposalForm.vue'
import { useGlobalStore } from '../../../stores/global-store'

export default defineComponent({
  name: 'SalesPipeline',
  components: {
    PipelineCard,
    ConfirmedPackageCard,
    ProposalView,
    ProposalForm,
  },
  setup() {
    const { init } = useToast()
    const globalStore = useGlobalStore()
    return { init, globalStore }
  },
  data() {
    return {
      seasonFilter: null as number | null,
      seasonOptions: [] as { value: number; text: string }[],
      showViewModal: false,
      showFormModal: false,
      editingProposal: null as any,
      isEditMode: false,
      preselectedInquiry: null as any,
    }
  },
  computed: {
    ...mapState(useProposalStore, ['pipeline', 'pipelineCounts', 'currentProposal', 'loading', 'saving']),
    counts(): any {
      return (
        this.pipelineCounts || {
          new_inquiries: 0,
          pending: 0,
          provision_sales: 0,
          confirmed: 0,
          cancelled: 0,
          completed: 0,
        }
      )
    },
    hasData(): boolean {
      return Object.values(this.counts).reduce((sum: number, count: any) => sum + count, 0) > 0
    },
  },
  mounted() {
    // Collapse sidebar to show full pipeline without horizontal scroll
    this.globalStore.isSidebarMinimized = true
    this.loadPipeline()
    this.loadSeasons()
  },
  methods: {
    ...mapActions(useProposalStore, [
      'fetchPipeline',
      'fetchProposalById',
      'createProposal',
      'updateProposal',
      'updateProposalStatus',
      'clearCurrentProposal',
    ]),

    async loadPipeline() {
      try {
        await this.fetchPipeline(undefined, this.seasonFilter || undefined)
      } catch (error) {
        this.init({
          message: 'Failed to load pipeline',
          color: 'danger',
        })
      }
    },

    async loadSeasons() {
      try {
        const url = `${import.meta.env.VITE_APP_BASE_URL}settings/seasons`
        const response = await axios.get(url)
        if (response.data) {
          const seasons = Array.isArray(response.data) ? response.data : response.data.data || []
          this.seasonOptions = seasons.map((s: any) => ({
            value: s.id,
            text: s.name,
          }))
        }
      } catch (error) {
        console.error('Error loading seasons:', error)
      }
    },

    handleCardClick(item: any) {
      if (item.type === 'proposal') {
        this.handleViewProposal(item)
      }
    },

    async handleViewProposal(item: any) {
      try {
        const proposalId = item.proposal_id || item.confirmation_id
        if (proposalId) {
          await this.fetchProposalById(proposalId)
          this.showViewModal = true
        } else {
          this.init({
            message: 'No quotation found for this item',
            color: 'warning',
          })
        }
      } catch (error) {
        this.init({
          message: 'Failed to load quotation details',
          color: 'danger',
        })
      }
    },

    handleEditProposal(item: any) {
      const proposalId = item.proposal_id || item.confirmation_id
      if (proposalId) {
        this.fetchProposalById(proposalId).then(() => {
          this.editingProposal = this.currentProposal
          this.isEditMode = true
          this.preselectedInquiry = null
          this.showFormModal = true
        })
      }
    },

    handleEditFromView() {
      this.showViewModal = false
      this.editingProposal = this.currentProposal
      this.isEditMode = true
      this.preselectedInquiry = null
      this.showFormModal = true
    },

    handleCreateProposal(inquiry: any) {
      this.editingProposal = null
      this.isEditMode = false
      this.preselectedInquiry = inquiry
      this.showFormModal = true
    },

    async handleSaveProposal(data: any) {
      try {
        if (this.isEditMode && this.editingProposal?.id) {
          await this.updateProposal(this.editingProposal.id, data)
          this.init({
            message: 'Quotation updated successfully',
            color: 'success',
          })
        } else {
          await this.createProposal(data)
          this.init({
            message: 'Quotation created successfully',
            color: 'success',
          })
        }
        this.showFormModal = false
        this.loadPipeline()
      } catch (error: any) {
        this.init({
          message: error.response?.data?.message || 'Failed to save quotation',
          color: 'danger',
        })
      }
    },

    async handleStatusChange(data: { id: number; status: string }) {
      try {
        await this.updateProposalStatus(data.id, data.status)
        this.init({
          message: `Proposal status changed to ${data.status}`,
          color: 'success',
        })
        await this.fetchProposalById(data.id)
        this.loadPipeline()
      } catch (error) {
        this.init({
          message: 'Failed to update proposal status',
          color: 'danger',
        })
      }
    },
  },
})
</script>

