<template>
  <div class="confirmed-package-card" @click="handleClick">
    <!-- Header with Status Badge -->
    <div class="card-header">
      <span class="package-code">{{ item.code }}</span>
      <VaChip size="small" color="success">
        <VaIcon name="verified" size="small" class="mr-1" />
        Confirmed
      </VaChip>
    </div>

    <!-- Client Info -->
    <div class="client-info">
      <h4 class="client-name">{{ item.client?.full_name || 'Unknown Client' }}</h4>
      <p class="client-meta">
        <VaIcon name="flag" size="small" />
        {{ item.client?.country || 'N/A' }}
      </p>
    </div>

    <!-- Package Details -->
    <div class="package-details">
      <div class="detail-row">
        <VaIcon name="inventory_2" size="small" color="success" />
        <span class="detail-label">Package:</span>
        <span class="detail-value">{{ item.hunting_details?.season || 'N/A' }}</span>
      </div>
      <div class="detail-row">
        <VaIcon name="location_on" size="small" color="success" />
        <span class="detail-label">Area:</span>
        <span class="detail-value">{{ item.hunting_details?.areas || 'N/A' }}</span>
      </div>
      <div class="detail-row">
        <VaIcon name="pets" size="small" color="success" />
        <span class="detail-label">Species:</span>
        <span class="detail-value truncate">{{ truncateSpecies(item.hunting_details?.species) }}</span>
      </div>
      <div class="detail-row">
        <VaIcon name="calendar_today" size="small" color="success" />
        <span class="detail-label">Dates:</span>
        <span class="detail-value">{{ formatDateRange }}</span>
      </div>
    </div>

    <!-- Amount -->
    <div v-if="item.total_amount" class="amount-section">
      <div class="amount-label">Total Amount</div>
      <div class="amount-value">{{ formatCurrency(item.total_amount) }}</div>
    </div>

    <!-- Sales Agent -->
    <div v-if="item.sales_agent" class="agent-info">
      <VaIcon name="support_agent" size="small" color="info" />
      <span class="agent-name">{{ item.sales_agent.name }}</span>
    </div>

    <!-- Confirmation Date -->
    <div class="confirmation-date">
      <VaIcon name="event_available" size="small" color="secondary" />
      <span>Confirmed: {{ item.created_date_formatted || formatDate(item.created_date) }}</span>
    </div>

    <!-- Actions -->
    <div class="card-actions">
      <VaButton size="small" preset="secondary" block @click.stop="$emit('view', item)">
        <VaIcon name="visibility" size="small" class="mr-1" />
        View Details
      </VaButton>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue'

interface PipelineItem {
  id: number
  type: 'inquiry' | 'proposal'
  stage: string
  code: string
  created_date: string
  created_date_formatted?: string
  client?: {
    id?: number
    full_name: string
    country: string
    email?: string
    phone?: string
  }
  hunting_details?: {
    season?: string
    areas: string
    species?: string
    start_date?: string
    end_date?: string
    no_of_days?: number
    no_of_hunters?: number
  }
  proposal_id?: number | null
  confirmation_id?: number | null
  sales_agent?: {
    id: number
    name: string
  } | null
  total_amount?: number | null
}

export default defineComponent({
  name: 'ConfirmedPackageCard',
  props: {
    item: {
      type: Object as PropType<PipelineItem>,
      required: true,
    },
  },
  emits: ['click', 'view'],
  computed: {
    formatDateRange(): string {
      const details = this.item.hunting_details
      if (!details?.start_date) return 'N/A'

      const formatDate = (dateStr: string) => {
        try {
          const date = new Date(dateStr)
          return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })
        } catch {
          return dateStr
        }
      }

      const start = formatDate(details.start_date)
      const end = details.end_date ? formatDate(details.end_date) : ''
      const days = details.no_of_days ? ` (${details.no_of_days} days)` : ''

      return end ? `${start} - ${end}${days}` : `${start}${days}`
    },
  },
  methods: {
    handleClick() {
      this.$emit('click', this.item)
    },

    truncateSpecies(species: string | undefined): string {
      if (!species) return 'N/A'
      if (species.length > 25) {
        return species.substring(0, 25) + '...'
      }
      return species
    },

    formatCurrency(amount: number | null | undefined): string {
      if (!amount) return '$0'
      return new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: 0,
        maximumFractionDigits: 0,
      }).format(amount)
    },

    formatDate(dateStr: string): string {
      try {
        const date = new Date(dateStr)
        return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })
      } catch {
        return dateStr
      }
    },
  },
})
</script>

